@font-face {
  font-family: 'CricketC';
  src: url("../fonts/CricketC.otf");
  src: url("../fonts/CricketC.ttf") format("truetype"); }

@font-face {
  font-family: 'Georgia';
  src: url("../fonts/Georgia.ttf") format("ttf");
  font-weight: normal;
  font-style: normal; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

html, body {
  min-height: 100%;
  font-family: Georgia, Verdana, serif;
  width: 100%;
  line-height: 1.42; }

.header {
  position: relative;
  height: 135px;
  background: white; }

.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  font-size: 0;
  text-align: center;
  height: 100%; }

.nav__item {
  display: inline-block;
  padding: 5% 0;
  width: 18%;
  height: 100%;
  vertical-align: top;
  font-family: CricketC; }

.nav__link {
  font-size: 20px;
  padding: 10% 0;
  font-family: CricketC;
  color: #c58ca0;
  text-decoration: none; }

.nav__link-active {
  color: black; }

.woman__legs {
  position: relative;
  text-align: left; }

.woman_img {
  max-width: 60vw;
  height: auto; }

.descr__container {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
