  z-index: 1;
  background: rgba(247, 243, 244, 0.5);
  padding: 0 7%; }

.descr__h1, .descr__h2 {
  text-align: center;
  margin: 0; }

.descr__h1 {
  font-size: 60px;
  letter-spacing: 4px;
  margin-top: 2%; }
