    .descr__h1 {
      font-size: 45px; } }
  @media screen and (max-width: 768px) {
    .descr__h1 {
      font-size: 30px; } }
  @media screen and (max-width: 480px) {
    .descr__h1 {
      font-size: 24px; } }

.descr__p {
  margin: 0;
  padding: 0;
  color: #c58ca0;
  font-size: 44px;
  text-align: right;
  margin-top: -3%; }
  @media screen and (max-width: 1200px) {
    .descr__p {
      font-size: 40px; } }
  @media screen and (max-width: 768px) {
    .descr__p {
      font-size: 28px; } }
  @media screen and (max-width: 480px) {
    .descr__p {
      font-size: 24px; } }

.descr__h2 {
  font-family: CricketC;
  color: #950a34;
  font-size: 50px;
  letter-spacing: 4px; }
  @media screen and (max-width: 1200px) {
