      font-size: 40px; } }
  @media screen and (max-width: 768px) {
    .descr__h2 {
      font-size: 22px;
      margin-top: -10px; } }

.man__body {
  position: relative;
  text-align: left;
  background: #EAE1E2;
  margin-top: 100px; }
  .man__body img {
    display: block; }

.market__item {
  position: absolute;
  text-align: left; }
  .market__item-fotoright {
    z-index: 1;
    background: rgba(247, 243, 244, 0.5);
